.container {
  max-width: 120rem;
  margin: 6rem auto 0;
  display: flex;
  flex-direction: column;
  gap: 6rem;
}

.flag {
  width: 4rem;
  height: 2rem;
}
h3 {
  text-align: center;
  font-size: 3.2rem;
}

@media (max-width: 48em) {
  .container {
    margin-top: 3rem;
    gap: 3rem;
  }
}
